import React, {useState} from 'react'
import { Button, Text } from '@pancakeswap/uikit'
import styled from 'styled-components'
import { Stack } from '@mui/material';
import { ToastDescriptionWithTx } from 'components/Toast';
import { useChonk } from "../../../hooks/useContract";
import useToast from "../../../hooks/useToast";
import { useTranslation } from "../../../contexts/Localization";
import useActiveWeb3React from "../../../hooks/useActiveWeb3React";


const Container = styled.div`
  border-radius: 16px;
  margin: 20px;
  background-color: ${({ theme }) => theme.colors.input};
  box-shadow: ${({ theme }) => theme.shadows.inset};
`

interface ClaimBnbPanelProps {
  value: string
  onSuccess: (value: string) => void
  usdtValue: string
}
export default function ClaimBnbPanel({
  value,
  onSuccess,
  usdtValue,
}: ClaimBnbPanelProps) {
  const { account } = useActiveWeb3React()
  const [isApproving, setIsApproving] = useState(false)
  const chonkContract = useChonk();
  const { toastSuccess, toastError } = useToast()
  const { t } = useTranslation()

  const handleReinvest = async () => {
    const tx = await chonkContract.reinvest()
    setIsApproving(true)
    const receipt = await tx.wait();
    if (receipt.status) {
      toastSuccess(
          t('Reinvest Successful'),
          <ToastDescriptionWithTx txHash={receipt.transactionHash}>
            {t('Your reinvest was successful!')}
          </ToastDescriptionWithTx>,
      )
      setIsApproving(false);
      onSuccess("0");
    } else {
      toastError(
          t('Error'),
          t('Please try again. Confirm the transaction and make sure you are paying enough gas!')
      );
      setIsApproving(false);
    }
  }

  const handleClaim = async () => {
    const tx = await chonkContract.withdraw();
    setIsApproving(true);
    const receipt = await tx.wait();
    if (receipt.status) {
      toastSuccess(
          t('Withdrawal Successful'),
          <ToastDescriptionWithTx txHash={receipt.transactionHash}>
            {t('Your withdrawal was successful!')}
          </ToastDescriptionWithTx>,
      )
      setIsApproving(false);
    } else {
      toastError(
          t('Error'),
          t('Please try again. Confirm the transaction and make sure you are paying enough gas!')
      );
      setIsApproving(false);
    }
  }

  return (
    <Container>
      <Stack justifyContent="space-between" style={{padding: "20px", height:"200px"}}>
        <Text bold fontSize="20px">Rewards</Text>
        <Text style={{paddingLeft: "6px"}}>{value} BNB (~ {usdtValue} USDT)</Text>
        <Stack direction="row" justifyContent="space-between">
          {!account ?
              <Button
                  disabled
                  scale="sm"
              >Reinvest</Button>
              :
              <Button
                  disabled={isApproving}
                  scale="sm"
                  variant="success"
                  onClick={handleReinvest}
              >Reinvest</Button>
          }
          {!account ?
              <Button
                  disabled
                  scale="sm"
              >Claim</Button>
              :
              <Button
                  disabled={isApproving}
                  scale="sm"
                  variant="danger"
                  onClick={handleClaim}
              >Claim</Button>
          }
        </Stack>
      </Stack>
    </Container>
  )
}
