import React from 'react'
import styled from 'styled-components'
import { CopyIcon, IconButton, Text } from '@pancakeswap/uikit'
import { VStack, HStack } from './Stacks'
import { useTranslation } from "../../../contexts/Localization";

const Container = styled.div`
  border-radius: 16px;
  margin: 20px;
  background-color: ${({ theme }) => theme.colors.input};
  box-shadow: ${({ theme }) => theme.shadows.inset};
`

const Address = styled.div`
  flex: 1;
  position: relative;

  & > input {
    background: transparent;
    border: 0;
    color: ${({ theme }) => theme.colors.text};
    display: block;
    font-weight: 600;
    font-size: 16px;
    padding: 0;
    width: 100%;

    &:focus {
      outline: 0;
    }
  }

  &:after {
    background: linear-gradient(
      to right,
      ${({ theme }) => theme.colors.background}00,
      ${({ theme }) => theme.colors.background}E6
    );
    content: '';
    height: 100%;
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 0;
    width: 40px;
  }
`

interface UserStatisticsProps {
  holdingsCHONK: string
  holdingsUSDT: string
  percentageOfPoolSize: string
  reflink: string
}

export default function UserStatistics({
  holdingsCHONK,
  holdingsUSDT,
  percentageOfPoolSize,
  reflink
}: UserStatisticsProps) {
  const { t } = useTranslation()

  return (
    <Container>
      <VStack spacing="0" style={{padding: "20px", height:"200px"}}>
        <Text bold fontSize="20px" style={{paddingBottom:"10px"}}>{t('My Holdings')}</Text>
        <Text>{holdingsCHONK} CHONK</Text>
        <Text>{percentageOfPoolSize}% of pool size</Text>
        <Text style={{marginTop: "20px"}}>{t('Earn 3% with your reflink')}:</Text>
        <HStack spacing="5">
          <Address title={reflink}>
            <input type="text" readOnly value={reflink} />
          </Address>
          <IconButton variant="text" onClick={() => navigator.clipboard.writeText(reflink)} style={{width: "32px"}}>
            <CopyIcon color="primary" width="20px" />
          </IconButton>
        </HStack>
      </VStack>
    </Container>
  )
}
