import React, {useState} from 'react'
import styled from 'styled-components'
import { useCookies } from 'react-cookie';
import { Stack } from '@mui/material';
import { Button, Text } from '@pancakeswap/uikit'
import { ethers } from "ethers";
import { escapeRegExp } from '../../../utils'
import { VStack, HStack } from './Stacks'
import { Input as NumericalInput } from '../../../components/CurrencyInputPanel/NumericalInput'

import { useChonk } from "../../../hooks/useContract";
import useToast from "../../../hooks/useToast";
import { useTranslation } from "../../../contexts/Localization";
import useActiveWeb3React from "../../../hooks/useActiveWeb3React";
import {
  FetchStatus,
  useGetBnbBalance,
  useGetChonkBalance
} from "../../../hooks/useTokenBalance";
import { ToastDescriptionWithTx } from "../../../components/Toast";

const Container = styled.div`
  border-radius: 16px;
  margin: 20px;
  background-color: ${({ theme }) => theme.colors.input};
  box-shadow: ${({ theme }) => theme.shadows.inset};
`

const inputRegex = RegExp(`^\\d*(?:\\\\[.])?\\d*$`) // match escaped "." characters via in a non-capturing group

interface TradeChonkInputPanelProps {
  label: string
  buttonLabel: string
  inputValue: string
  onChange: (value: string) => void
  onMax: (value: string) => void
  currency: string
  secondaryValue: string
  secondaryCurrency: string
  valueUSDT: string
  hasGreenButton: boolean
}
export default function TradeChonkInputPanel({
  label,
  buttonLabel,
  inputValue,
  onChange,
  onMax,
  currency,
  secondaryValue,
  secondaryCurrency,
  valueUSDT,
  hasGreenButton,
}: TradeChonkInputPanelProps) {
  const enforcer = (nextUserInput: string) => {
    if (nextUserInput === '' || inputRegex.test(escapeRegExp(nextUserInput))) {
      onChange(nextUserInput)
    }
  }
  const { account } = useActiveWeb3React()
  const { balance, fetchStatus } = useGetBnbBalance()
  const { chonkBalance, chonkFetchStatus } = useGetChonkBalance()
  const [isApproving, setIsApproving] = useState(false)
  const chonkContract = useChonk();
  const { toastSuccess, toastError } = useToast()
  const { t } = useTranslation()
  const [cookies] = useCookies(['Refaddress', 'Refselection']);

  const handleApprove = async () => {
    let tx;
    if (hasGreenButton) {
      let ref = '';
      switch (cookies.Refselection) {
        case "two":
          ref = cookies.Refaddress;
          break;
        case "three": 
          ref = '0x0000000000000000000000000000000000000000'; // Project ADDRESS
          break;
        case "four": 
          ref = '0x0000000000000000000000000000000000000000'; // CHONK ADDRESS
          break;
        default:
          ref = '0x0000000000000000000000000000000000000000'
      }
      const overrides = {
        value: ethers.utils.parseEther(inputValue.replace(',', '.')),
      };
      tx = await chonkContract.buy(ref, overrides)
      
    } else {
      const sellValue = ethers.utils.parseEther(inputValue.replace(',', '.'));
      tx = await chonkContract.sell(sellValue)
    }
    setIsApproving(true);
    const receipt = await tx.wait();
    if (receipt.status) {
      toastSuccess(
          hasGreenButton ? t('Buy Successful') : t('Sell Successful'),
          <ToastDescriptionWithTx txHash={receipt.transactionHash}>
            {hasGreenButton ? t('Your CHONK buy was successful!') : t('Your CHONK sell was successful!')}
          </ToastDescriptionWithTx>,
      )
      setIsApproving(false);
    } else {
      // user rejected tx or didn't go thru
      toastError(t('Error'), t('Please try again. Confirm the transaction and make sure you are paying enough gas!'));
      setIsApproving(false);
    }
  }

  const handleMax = async () => {
    if (fetchStatus === FetchStatus.SUCCESS && currency === "BNB") {
      onMax(ethers.utils.formatEther(balance));
    } else if (chonkFetchStatus === FetchStatus.SUCCESS && currency === "CHONK") {
      onMax(ethers.utils.formatEther(chonkBalance));
    }
  }

  return (
    <Container>
      <VStack spacing="10" style={{padding: "20px", height:"200px"}}>
        <Text bold fontSize="20px" style={{paddingBottom:"10px"}}>{label} {currency}</Text>
        <Stack spacing={3} direction="row" justifyContent="space-between">
          <HStack spacing="2">
            <HStack spacing="5" style={{borderWidth: "1px", borderStyle: "solid", borderRadius: "7px", paddingInline: "7px", width: "100%"}}>
              <NumericalInput style={{minWidth:"50px", width: "100%"}} value={inputValue} onUserInput={(input) => {enforcer(input)}}/>
              <Button scale="sm" variant="text" style={{padding: "0px"}} onClick={handleMax}>Max</Button>
            </HStack>
            <Text>{currency}</Text>
          </HStack>
          {!account ?
              <Button
                  disabled
                  scale="sm"
              >{buttonLabel}</Button>
              :
              <Button
                  style={{width: "81px"}}
                  disabled={isApproving}
                  scale="sm"
                  variant={hasGreenButton ? "success" : "danger"}
                  onClick={handleApprove}
              >{buttonLabel}</Button>
          }

        </Stack>
        <VStack spacing="0" style={{paddingLeft: "6px"}}>
          <Text fontSize="14px">~ {secondaryValue} {secondaryCurrency}</Text>
          <Text fontSize="14px">~ {valueUSDT} USDT</Text>
        </VStack>
      </VStack>
    </Container>
  )
}
