import React, { useState, useEffect, useCallback } from 'react'
import { useLocation } from 'react-router-dom';
import { useCookies } from 'react-cookie';
import { Grid } from '@mui/material';

import { Radio, Text } from '@pancakeswap/uikit'
import { VStack } from './Stacks'

const addressRegex = RegExp(`^[?]ref[=]0x[a-zA-Z0-9]{40}$`)
const cookieExpirationDate = new Date(Date.UTC(new Date().getUTCFullYear() + 420, new Date().getUTCMonth()));

interface ReflinkProps {
    name: string
    value: string
    checked: boolean
    onChange: (value: string) => void
}
function Reflink({
    name,
    value,
    checked,
    onChange
}: ReflinkProps) {
    return (
        <VStack spacing="0" style={{ alignItems: "center", width: "100%"}}>
            <Radio scale="sm" value={value} onChange={(e) => onChange(e.target.value)} checked={checked} style={{backgroundColor: "#B8ADD2"}}/>
            <Text>{name}</Text>
        </VStack>
    )
}

export default function ReflinkSelection() {
    const [location] = useState(useLocation());
    const [cookies, setCookie] = useCookies(['Refaddress', 'Refselection']);
    const [checked, setChecked] = useState(cookies.Refselection);

    const handleChange = useCallback((value) => {
        setCookie('Refselection', value, { path: '/', expires: cookieExpirationDate });
        setChecked(value);
    }, [setCookie]);

    useEffect(() => {
        if (cookies.Refaddress === undefined && addressRegex.test(location.search)) {
            setCookie('Refaddress', location.search.replace("?ref=", ""), { path: '/', expires: cookieExpirationDate })
            setCookie('Refselection', "two", { path: '/', expires: cookieExpirationDate })
            setChecked("two");
          }
        if (cookies.Refselection === undefined) {
            handleChange("four");
        }
    }, [location, cookies, setCookie, setChecked, handleChange]);

    return(
        <Grid container spacing={1} style={{padding: "10px"}}>
            <Grid item xs={6} md={cookies.Refaddress === undefined ? 4 : 3}>
                <Reflink name="No Referal" value="one"  checked={checked === "one"} onChange={handleChange}/>
            </Grid>
            {cookies.Refaddress !== undefined ? <Grid item xs={6} md={3}>
                <Reflink name="Origin Referal" value="two" checked={checked === "two"} onChange={handleChange}/>
            </Grid> : null}
            <Grid item xs={6} md={cookies.Refaddress === undefined ? 4 : 3}>
                <Reflink name="Support <Project>" value="three" checked={checked === "three"} onChange={handleChange}/>
            </Grid>
            <Grid item xs={cookies.Refaddress === undefined ? 12 : 6} md={cookies.Refaddress === undefined ? 4 : 3}>
                <Reflink name="Support CHONK" value="four" checked={checked === "four"} onChange={handleChange}/>
            </Grid>
        </Grid>
    )
}