import React, {useState, useEffect} from 'react'
import { Grid } from '@mui/material';

import { getTimeRemaining, isCountdownDone } from 'utils/countdownCalculations'

import {Card, Text} from '@pancakeswap/uikit'
import styled, {keyframes} from "styled-components";
import { ethers } from "ethers";
import Page from '../Page'

import TradeChonkInputPanel from './components/TradeChonkInputPanel'
import ClaimBnbPanel from './components/ClaimBnbPanel'
import UserStatistics from './components/UserStatistics'
import { VStack } from './components/Stacks'
import { useTranslation } from "../../contexts/Localization";
import { useChonk } from "../../hooks/useContract";
import { getSrcSet } from "../Home/components/CompositeImage";
import ReflinkSelection from './components/ReflinkSelection';
import useActiveWeb3React from "../../hooks/useActiveWeb3React";

const flyingAnim = () => keyframes`
  from {
    transform: translate(0,  0px);
  }
  50% {
    transform: translate(-5px, -5px);
  }
  to {
    transform: translate(0, 0px);
  }  
`

const ChonkWrapper = styled.div`
  width: 25%;
  animation: ${flyingAnim} 3.5s ease-in-out infinite;
`
const imagePath = '/images/home/lazy-chonk/'
const imageSrc = 'chonk'

export default function Swap() {
  const [countdown, setCountdown] = useState(getTimeRemaining());
  const [bnbValue, setBnbValue] = useState("");
  const [bnbBuyValue, setBnbBuyValue] = useState(0);
  const [bnbSellValue, setBnbSellValue] = useState(0);
  const [chonkValue, setChonkValue] = useState("");
  const [bnbRewards, setBnbRewards] = useState("0");
  const [chonkHoldings, setChonkHoldings] = useState("0");
  const [poolShare, setPoolShare] = useState("0");
  const { account } = useActiveWeb3React();
  const [personalReflink, setPersonalReflink] = useState("");
  const { t } = useTranslation()
  const chonkContract = useChonk();

  useEffect(() => {
    const timer = !isCountdownDone(account) && setInterval(() => setCountdown(getTimeRemaining()), 1000);
    return() => clearInterval(timer);
  }, [account]);

  useEffect(() => {
    const getData = async () => {
      setPersonalReflink(`${window.location.protocol}//${window.location.host}/swap?ref=${account}`);
      const promises = [
          chonkContract.myTokens(),
          chonkContract.myDividends(true),
          chonkContract.totalSupply()
      ];
      await Promise.all(promises).then((values) => {
        const chonkHold = Number(ethers.utils.formatEther(BigInt(values[0]._hex)));
        const bnbReward = Number(ethers.utils.formatEther(BigInt(values[1]._hex)));
        const totalSupply = Number(ethers.utils.formatEther(BigInt(values[2]._hex)))
        setChonkHoldings(chonkHold.toFixed(2));
        setBnbRewards(bnbReward.toFixed(4));
        setPoolShare(((chonkHold/totalSupply) * 100).toFixed(2));
      });
    }
    getData();
  })

  useEffect(() => {
    const getData = async () => {
      const promises = [chonkContract.buyPrice()];
      await Promise.all(promises).then((values) => {
        const buyPrice = Number(ethers.utils.formatEther(BigInt(values[0]._hex)));
        setBnbBuyValue(Number(bnbValue)/buyPrice);
      });
    }
    getData();
  }, [bnbValue, chonkContract])

  useEffect(() => {
    const getData = async () => {
      const sellValue = ethers.utils.parseEther(chonkValue.replace(',', '.'));
      const promises = [chonkContract.calculateBnbReceived(sellValue)];
      await Promise.all(promises).then((values) => {
        setBnbSellValue(Number(ethers.utils.formatEther(BigInt(values[0]._hex))));

      });
    }
    getData();
  }, [chonkValue, chonkContract])

  return (
    <Page>
      {isCountdownDone(account) ?
      <Grid container spacing={4} justifyContent="center" style={{maxWidth: "950px"}}>
        <Grid item xs={12} sm={7} md={6} >
            <Card>
              <Grid container>
                <Grid item style={{width: "100%"}}>
                  <TradeChonkInputPanel
                    label={t('Spend')}
                    buttonLabel={t('Spend')}
                    inputValue={bnbValue}
                    onChange={setBnbValue}
                    onMax={setBnbValue}
                    currency="BNB"
                    secondaryValue={bnbBuyValue.toFixed(4)}
                    secondaryCurrency="CHONK"
                    valueUSDT={(Number(bnbValue)*550).toFixed(1)}
                    hasGreenButton
                  />
                </Grid>
                <Grid item style={{width: "100%"}}>
                  <TradeChonkInputPanel
                    label={t('Sell and Claim')}
                    buttonLabel={t('Sell')}
                    inputValue={chonkValue}
                    onChange={setChonkValue}
                    onMax={setChonkValue}
                    currency="CHONK"
                    secondaryValue={bnbSellValue.toFixed(4)}
                    secondaryCurrency="BNB"
                    valueUSDT={(Number(bnbSellValue)*550).toFixed(1)}
                    hasGreenButton={false}
                  />
                </Grid>
              </Grid>
            </Card>
        </Grid>
        <Grid item xs={12} sm={7} md={6}>
            <Card>
              <Grid container>
                <Grid item style={{width: "100%"}}>
                  <ClaimBnbPanel
                    value={bnbRewards}
                    onSuccess={setBnbRewards}
                    usdtValue={(Number(bnbRewards)*550).toFixed(1)}
                  />
                </Grid>
                <Grid item style={{width: "100%"}}>
                  <UserStatistics
                    holdingsCHONK={chonkHoldings}
                    holdingsUSDT="0"
                    percentageOfPoolSize={poolShare}
                   reflink={personalReflink}
                  />
               </Grid>
              </Grid>
            </Card>
        </Grid>
        <Grid item xs={12} sm={7} md={12}>
          <Card>
            <ReflinkSelection/>
          </Card>
        </Grid>
      </Grid> :
      <VStack spacing="-20" style={{paddingTop:"50px", alignItems: "center"}}>
        <ChonkWrapper>
          <img src={`${imagePath}${imageSrc}.png`} srcSet={getSrcSet(imagePath, imageSrc)} alt={t('Lazy chonk')} />
        </ChonkWrapper>
        <br/><br/><br/>
        <Text fontSize="75px" >{t('Testnet launch in')}</Text>
        <Text fontSize="100px">{countdown}</Text>
        <Text fontSize="35px" color="gray">{t('28th December 2021 18:00')} UTC</Text>
      </VStack>
      }
    </Page>
  )
}
